<template>
  <div class="footer">
    <img class="icon" src="@/assets/images/logo-footer@2x.png" alt="">
    <div class="copy-right">
      {{$t('index.Copyright©KEYA KEYB 版權所有',{KEYA:curYear,KEYB:'Mole Tera'})}}
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import { useI18n } from "vue-i18n";
export default {
  setup () {
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      curYear: (new Date()).getFullYear()
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.footer {
  background: #1a66b4;
  height: 185px;
  padding: 25px 15px;
  .icon {
    width: 162px;
    height: auto;
    display: block;
    margin: 0 auto;
    padding-bottom: 10px;
  }
  .copy-right {
    width: 162px;
    margin: 0 auto;
    font-size: 13px;
    line-height: 1.5;
    word-break: break-word;
    color: #7abcff;
    text-align: center;
  }
}
</style>
