import { createStore } from "vuex";
export default createStore({
  state: {
    accounts: [],
    provider: null,
    currentBalance: "",
    locale: "",
    initem: "",
    price: "",
    chartData: [],
    isPlegdeOut: true,
    dialogMessage: {
      title: "",
      content: "",
      isShow: false
    }
  },
  mutations: {
    dialogMessage(state, value) {
      value.isShow = true;
      state.dialogMessage = value;
    },
    updateAccount(state, value) {
      state.accounts = value;
    },
    updateProvider(state, value) {
      state.provider = value;
    },
    updateCurrentBalance(state, value) {
      state.currentBalance = value;
    },
    updateLocale(state, value) {
      state.locale = value;
    },
    updatePrice(state, value) {
      state.price = value;
    },
    initChartData(state, value) {
      state.chartData = [...value, ...state.chartData];
    },
    updateChartData(state, value) {
      state.chartData.push(value);
    },
    updateInitem(state, value) {
      state.initem = value;
    },
    updateIsPlegdeOut(state, value) {
      state.isPlegdeOut = !!value;
    }
  },
  actions: {},
  modules: {}
});
