import { createI18n } from "vue-i18n";
import { locale } from "../storage";
import store from "../store";
import zh_TW from "../locales/zh-TW.json";
import en from "../locales/en.json";
import { Local } from "./vantLocale.js";
// let jsonObj = zh_TW;
export const i18n = createI18n({
  locale: "zh-TW",
  messages: {
    "zh-TW": {
      ...zh_TW
    },
    en: {
      ...en
    }
  }
  // missing: (locale, key) => {
  //   // console.log(locale, key);
  //   let flag = key.indexOf(".");
  //   if (flag !== -1) {
  //     let strArr = key.split(".");
  //     console.log(strArr);
  //     jsonObj[strArr[0]][strArr[1]] = strArr[1];
  //   } else {
  //     jsonObj[key] = key;
  //   }
  //   console.log(JSON.stringify(jsonObj));
  // }
});

export const setLang = lang => {
  i18n.global.locale = lang;
  store.commit("updateLocale", lang);
  locale.set(lang);
  Local(lang);
};

export const getLang = () => {
  return i18n && i18n.global.locale;
};
